@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scrollbar-width: none;
}
html::-webkit-scrollbar {
  display: none;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  object-fit: contain;
}

