.card__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.card__price {
  margin: 0;
}
.card__price dd {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.6;
  color: #333333;
}

.card__button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 141px;
  min-height: 36px;
  font-size: 14px;
  line-height: 1.43;
}

.pagination__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
@media (min-width: 768px) {
  .pagination__list {
    justify-content: flex-end;
  }
}

.pagination__button {
  width: 40px;
}
@media (min-width: 768px) {
  .pagination__button {
    padding-left: 20px;
    padding-right: 20px;
    width: max-content;
  }
}
.pagination__button--next svg {
  transform: rotate(180deg);
}
@media (min-width: 768px) {
  .pagination__button--next svg {
    display: none;
  }
}
.pagination__button--disabled {
  background-color: #e2e2e2;
  pointer-events: none;
  color: #ffffff;
}
@media (min-width: 768px) {
  .pagination__button--disabled {
    visibility: hidden;
  }
}
@media (min-width: 768px) {
  .pagination__button svg {
    display: none;
  }
}
.pagination__button span {
  display: none;
}
@media (min-width: 768px) {
  .pagination__button span {
    display: block;
    font-size: 14px;
    line-height: 1.72;
  }
}

.pagination__numbers {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.pagination__numbers-button {
  width: 40px;
}
.pagination__numbers-button--current {
  color: #333333;
  background-color: transparent;
  font-weight: 600;
  pointer-events: none;
}
.pagination__numbers-button--disabled {
  background-color: #e2e2e2;
  pointer-events: none;
  color: #ffffff;
}

.map {
  position: relative;
  height: 420px;
}
@media (min-width: 768px) {
  .map {
    height: 480px;
  }
}
@media (min-width: 1440px) {
  .map {
    height: 540px;
  }
}

.map__picture {
  display: grid;
  margin: 0;
}
.map__picture img {
  width: 100%;
  object-fit: cover;
}
@media (min-width: 768px) {
  .map__picture img {
    height: 480px;
  }
}
@media (min-width: 1440px) {
  .map__picture img {
    height: 540px;
  }
}

.map__inter {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  z-index: 30;
  width: 100%;
  height: 420px;
}
@media (min-width: 768px) {
  .map__inter {
    height: 480px;
  }
}
@media (min-width: 1440px) {
  .map__inter {
    height: 540px;
  }
}

.footer {
  padding-left: 0;
  padding-right: 0;
}
@media (min-width: 768px) {
  .footer {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 40px;
    padding-bottom: 40px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, auto);
    gap: 30px;
  }
}
@media (min-width: 1440px) {
  .footer {
    padding: 40px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, auto);
    row-gap: 20px;
  }
}

.footer__social {
  padding-left: 20px;
  padding-right: 20px;
  min-height: 80px;
  border-bottom: 1px solid #F0EBFF;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .footer__social {
    padding: 0;
    grid-column: 1/-1;
    min-height: unset;
    border: none;
  }
}
@media (min-width: 1440px) {
  .footer__social {
    grid-column: 1/2;
    flex-direction: column;
    gap: 50px;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.footer__logo {
  margin: 0;
  flex-shrink: 0;
}
@media (min-width: 1440px) {
  .footer__logo {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}
.footer__logo img {
  display: block;
}
@media (min-width: 768px) {
  .footer__logo img {
    width: 170px;
    height: 36px;
  }
}
.footer__logo figcaption {
  display: none;
}
@media (min-width: 1440px) {
  .footer__logo figcaption {
    display: block;
    font-size: 14px;
    line-height: 1.43;
  }
}

.footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  gap: 20px;
}

.footer__social-link {
  text-decoration: none;
  width: 40px;
  height: 40px;
  border-radius: 4px;
  display: grid;
  place-items: center;
  color: #BDBDBD;
  transition: background-color 0.13s ease-in-out, color 0.13s ease-in-out;
}
.footer__social-link:hover {
  color: #7859CF;
  background-color: #F0EBFF;
}
.footer__social-link:active {
  background-color: rgba(120, 89, 207, 0.25);
}
.footer__social-link:focus-visible {
  outline: none;
  color: #7859CF;
  background-color: #F0EBFF;
}

.footer__section {
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  border-bottom: 1px solid #F0EBFF;
}
@media (min-width: 768px) {
  .footer__section {
    padding: 0;
    border: none;
  }
}
.footer__section--contacts {
  gap: 19px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.43;
}
.footer__section--contacts address {
  font-style: normal;
  display: flex;
  align-items: flex-start;
  gap: 6px;
}
.footer__section--contacts address::before {
  position: relative;
  content: "";
  top: 3px;
  width: 13px;
  height: 16px;
  background-image: url("/img/address-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
  flex-shrink: 0;
}
.footer__section--contacts a {
  display: flex;
  align-items: center;
  gap: 6px;
  text-decoration: none;
  color: #333333;
  transition: color 0.13s ease-in-out;
}
.footer__section--contacts a:hover {
  color: #7859CF;
}
.footer__section--contacts a:active {
  color: #593CAC;
}
.footer__section--contacts a:focus-visible {
  outline: none;
  color: #7859CF;
}
.footer__section--contacts a::before {
  content: "";
  width: 13px;
  height: 16px;
  background-image: url("/img/tel-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
  flex-shrink: 0;
}

.footer__title {
  font-size: 14px;
  font-weight: 700;
  line-height: 1.43;
  letter-spacing: 0.02em;
  color: #7859CF;
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.footer__list-item a {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.34;
  letter-spacing: 0.02em;
  height: 32px;
  display: flex;
  align-items: center;
  color: #333333;
  transition: color 0.13s ease-in-out;
}
.footer__list-item a:hover[href] {
  color: #9070EC;
}
.footer__list-item a:active[href] {
  color: #593CAC;
}
.footer__list-item a:focus-visible[href] {
  outline: none;
  color: #9070EC;
}

.footer__academy {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
  padding: 20px 20px 30px;
  font-size: 12px;
  line-height: 1.34;
  color: #BDBDBD;
}
@media (min-width: 768px) {
  .footer__academy {
    padding: 0;
    grid-column: 1/-1;
  }
}
.footer__academy a {
  display: grid;
  place-items: center;
  color: #BDBDBD;
  transition: color 0.13s ease-in-out;
}
.footer__academy a:hover {
  color: #7859CF;
}
.footer__academy a:active {
  color: #593CAC;
}
.footer__academy a:focus-visible {
  outline: none;
  color: #7859CF;
}